import React from 'react';
import { Subscribe } from 'unstated';
import { PlayList } from './../../handlers/playlist';

import searchIcon from './ic_search_black_24px.svg';
import './search.css';

class Search extends React.Component {
    state = {
        condition: false
    };

    imgClickHandler(event) {
        this.setState({
            condition: !this.state.condition
        })

        if (!this.state.condition) {
            this.inputBox.focus();
        }
    }

    filterUpdateHandler(event) {
        if (event.key === 'Enter')
        {
            this.setState({
                condition: false
            })
        }
    }

    render() {
        return (
            <Subscribe to={[PlayList]}>
                {playlist => (
                    <div className="search-container">
                        <input ref={(input) => {this.inputBox = input}}
                            className={this.state.condition ? "search-box" : "search-box close"}
                            onChange={(event) => playlist.updateFilter(event.target.value)}
                            onKeyPress={(event) => this.filterUpdateHandler(event)}>
                        </input>
                        <img src={searchIcon} className="search-logo" alt="search" onClick={(event) => this.imgClickHandler(event)} />
                    </div>
                )}
            </Subscribe>
        );
    }
}

export default Search;


