import React from 'react';
import { Subscribe } from 'unstated';
import { PlayList } from './../../handlers/playlist';

import './left.css';

class Left extends React.Component {
    render() {
        return (
            <Subscribe to={[PlayList]}>
                {playlist => (
                    <div className={playlist.state.open ? "left" : "left closed"}>
                        <div className="playing-next">
                            <div className="cover-art"></div>
                            <div className="playing-next-info">
                                <p className="playing-next-header"> { playlist.state.filter } </p>
                                <p className="playing-next-title">Rubber Duck S.W.A.G.</p>
                                <p className="playing-next-author">By Rubber Duck S.W.A.G.</p>
                            </div>
                        </div>
                    
                    
                    </div>
                )}
            </Subscribe>
        );
    }
}

export default Left;

