// @flow
import { Container } from 'unstated';

class PlayList extends Container {
  state = {
    open: true,
    filter: "Test"
  }

  togglePlayList() {
    this.setState({
        open: !this.state.open
    });
  }

  updateFilter(filterText) {
    this.setState({
      filter: filterText
  });
  }
}

export {
    PlayList
}


